import Vue from "vue";

import {
  message,
  Button,
  Card,
  Row,
  Alert,
  Col,
  Icon,
  Tag,
  Table,
  Menu,
  Layout,
  Tooltip,
  Space,
} from "ant-design-vue";

import BaseLayout from './layout/index';

const components = [
  BaseLayout,
  Button,
  Card,
  Row,
  Alert,
  Col,
  Icon,
  Tag,
  Table,
  Menu,
  Menu.Item,
  Layout,
  Layout.Header,
  Tooltip,
  Icon,
  Space,
  Card,
];

const registerComponent = (name, component) => Vue.component(name, component);

for (const component of components) {
  registerComponent(component.name, component);
}

Vue.prototype.$message = message;
