import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'harvest',
    component: () => import( '../views/Harvest.vue')
  },
  {
    path: '/card',
    name: 'card',
    component: () => import( '../views/Card.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
